export default function Blog() {
  const classes = useStyles();
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    const fetchMarkdownFiles = async () => {
      const files = [
        '/blog-post.1.md',
        '/blog-post.2.md',
        '/blog-post.3.md',
        '/blog-attitude.md',
        '/blog-mood.md',
        '/blog-web.md',
        '/blog-community.md',
        '/blog-say.md'
      ];
      const promises = files.map((file) =>
        fetch(file, { headers: { 'Content-Type': 'text/plain; charset=utf-8' } }).then((response) => response.text())
      );
      const markdownContents = await Promise.all(promises);
      setPosts(markdownContents);
    };

    fetchMarkdownFiles();
  }, []);

  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth="lg">
        <Header title="WeHelp申請" sections={sections} />
        <main>
          <MainFeaturedPost post={mainFeaturedPost} />
          <Grid container spacing={4}>
            {featuredPosts.map((post) => (
              <FeaturedPost key={post.title} post={post} />
            ))}
          </Grid>
          <Grid container spacing={5} className={classes.mainGrid}>
            <Main title="問題回答" posts={posts} />
            <Sidebar
              title={sidebar.title}
              description={sidebar.description}
              archives={sidebar.archives}
              social={sidebar.social}
            />
          </Grid>
        </main>
      </Container>
      <Footer title="" description="希望可以順利錄取" />
    </React.Fragment>
  );
}
